import { Component, Event, EventEmitter, Prop, h } from '@stencil/core';
import { RatePlan, Variation } from '@/models/property';
import app_store from '@/stores/app.store';
import { IRatePlanSelection, reserveRooms, updateRoomParams } from '@/stores/booking';
import { formatAmount } from '@/utils/utils';
import localizedWords from '@/stores/localization.store';

@Component({
  tag: 'ir-rateplan',
  styleUrl: 'ir-rateplan.css',
  shadow: true,
})
export class IrRateplan {
  @Prop() ratePlan: RatePlan;
  @Prop() visibleInventory?:
    | IRatePlanSelection
    | {
        reserved: number;
        visibleInventory?: number;
        selected_variation: any;
      };
  @Prop() roomTypeInventory: number;
  @Prop() roomTypeId: number;

  @Event() animateBookingButton: EventEmitter<null>;

  handleVariationChange(e: CustomEvent, variations: Variation[], rateplanId: number, roomTypeId: number) {
    e.stopImmediatePropagation();
    e.stopPropagation();
    const value = e.detail;
    console.log(value, variations);
    const selectedVariation = variations.find(v => (v.adult_nbr + v.child_nbr).toString() === value);
    console.log('selectedVariation:', selectedVariation);
    if (!selectedVariation) {
      return;
    }
    updateRoomParams({ params: { selected_variation: selectedVariation }, ratePlanId: rateplanId, roomTypeId });
  }
  render() {
    return (
      <div class="rateplan-container">
        <div class={`rateplan-header ${this.roomTypeInventory > 0 ? 'available' : 'not-available'}`}>
          <p class="rateplan-name">
            <span class="rateplan-short-name">{this.ratePlan.short_name}</span>
            <span class="rateplan-custom-text rateplan-custom-text-hidden">{this.ratePlan.custom_text}</span>
          </p>
          {this.roomTypeInventory > 0 ? (
            <div class="rateplan-pricing-mobile">
              <p class="rateplan-amount">{formatAmount(this.visibleInventory?.selected_variation?.amount, app_store.userPreferences.currency_id, 0)}</p>
              {this.visibleInventory?.selected_variation?.discount_pct > 0 && (
                <p class="rateplan-discounted-amount">{formatAmount(this.visibleInventory?.selected_variation?.total_before_discount, app_store.userPreferences.currency_id, 0)}</p>
              )}
            </div>
          ) : (
            <p class="no-availability">Not available</p>
          )}
        </div>

        <p class="rateplan-custom-text rateplan-custom-text-mobile">{this.ratePlan.custom_text}</p>

        {this.roomTypeInventory > 0 && (
          <div class={`rateplan-details ${this.ratePlan.custom_text ? 'rateplan-details-no-custom-text' : ''}`}>
            <div class="rateplan-travelers">
              {this.ratePlan.variations && (
                <ir-select
                  class="rateplan-select-travelers"
                  label={localizedWords.entries.Lcz_Travelers}
                  value={(this.visibleInventory?.selected_variation?.adult_nbr + this.visibleInventory?.selected_variation?.child_nbr).toString()}
                  onValueChange={e => {
                    this.handleVariationChange(e, this.ratePlan.variations, this.ratePlan.id, this.roomTypeId);
                  }}
                  data={this.ratePlan.variations.map(v => ({
                    id: (v.adult_nbr + v.child_nbr).toString(),
                    value: v.adult_child_offering,
                  }))}
                ></ir-select>
              )}
              <div class="rateplan-cancellation ">
                <p class="rateplan-cancellation-text">If I cancel?</p>
                <ir-tooltip class="rateplan-tooltip" message={this.ratePlan.cancelation + '<br>' + this.ratePlan.guarantee}></ir-tooltip>
              </div>
            </div>
            {this.visibleInventory?.selected_variation?.discount_pct > 0 && (
              <div class="rateplan-pricing">
                <p class="rateplan-discounted-amount">{formatAmount(this.visibleInventory?.selected_variation?.total_before_discount, app_store.userPreferences.currency_id, 0)}</p>
                <p class="rateplan-discount">{`-${this.visibleInventory?.selected_variation?.discount_pct}%`}</p>
              </div>
            )}
            <div class="rateplan-final-pricing">
              <p class="rateplan-amount">{formatAmount(this.visibleInventory?.selected_variation?.amount, app_store.userPreferences.currency_id, 0)}</p>
              <p class="rateplan-amount-per-night">{`${formatAmount(this.visibleInventory?.selected_variation?.amount_per_night, app_store.userPreferences.currency_id, 0)}/${localizedWords.entries.Lcz_night}`}</p>
            </div>

            <ir-select
              onValueChange={e => {
                reserveRooms(this.roomTypeId, this.ratePlan.id, Number(e.detail));
                this.animateBookingButton.emit(null);
              }}
              label={localizedWords.entries.Lcz_Rooms}
              value={this.visibleInventory?.reserved}
              class="rateplan-select-rooms"
              data={[...new Array(this.roomTypeInventory + 1)]?.map((_, i) => ({
                id: i,
                value:
                  i === 0
                    ? `${localizedWords.entries.Lcz_Select}`
                    : `${i}&nbsp;&nbsp;&nbsp;${i === 0 ? '' : formatAmount(this.visibleInventory?.selected_variation?.amount * i, app_store.userPreferences.currency_id, 0)}`,
                disabled: i >= this.visibleInventory?.visibleInventory + 1,
                html: true,
              }))}
            ></ir-select>
          </div>
        )}
      </div>
    );
  }
}
